/* ////////////////////////////////////
////////ABOUT페이지////////////////// 
////////////////////////////////////////////////////////////////////////////////////*/

#about {
    display: flex;
    align-items: center;
    width: 100%;
    height: 950px;
    box-sizing: border-box;
    scroll-margin-top: 80px;
    /* padding-top: 124px; */

}

.aboutContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    box-sizing: border-box;
}

.aboutLeft {
    width: 364px;
}

.aboutLeftImage {
    width: 364px;
    height: 525px;
    background-color: #dddddd;
    border-radius: 10px;
    margin-bottom: 10px;
}

.aboutLeftImage img {
    width: 100%;
    height: 100%;
    object-fit: fill;
    border-radius: 10px;

}

.aboutLeftiInfo {
    width: 364px;
}

.aboutLeftiInfo li {
    font-size: 25px;
    color: #364e66;
    margin-bottom: 10px;
}



.aboutCenter {
    width: 510px;
    height: 747px;
    border-bottom: 1px solid #364e66;
    border-top: 1px solid #364e66;
    padding-top: 10px;
    margin: 0 45px;

}

.aboutCenter h2,
.aboutRight h2 {
    font-size: 45px;
    color: #364e66;
    font-weight: 800;
}

.aboutCenter h2 {
    margin-bottom: 19px;
}

.aboutCenter li {
    font-size: 23px;
    color: #000000;
    font-weight: 500;
}

.education ul,
.career ul {
    margin-bottom: 85px;
}

.education li:first-child {
    margin-bottom: 25px;
}

.education li:nth-child(1) span {
    margin-left: 5px;
}

.education li:nth-child(2) span {
    margin-left: 5px;
}

.career span {
    margin-left: 2px;
}

.training li:first-child {
    margin-bottom: 15px;
}

.training li:last-child {
    line-height: 1.55;
}

.aboutRight {
    width: 420px;
    height: 747px;
    border-bottom: 1px solid #364e66;
    border-top: 1px solid #364e66;
    padding-top: 10px;
}

.aboutRight h2 {
    margin-bottom: 33px;
}

.aboutRight ul {
    margin-bottom: 58px;
}

.SkillWrap {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    /* 왼/오 2열 */
    column-gap: 36px;
    /* 좌우 간격 */
    row-gap: 28px;
    /* 세로 간격(행 간) */
    align-items: start;
}



.skillsContainer,
.skillsContainer02 {
    display: flex;
    flex-direction: column;
    /* gap: 10px; */
}

.skillList {
    display: flex;
    gap: 20px;
}

.skillList li {
    display: flex;
    align-items: center;
    gap: 20px;
}

.skillHeader {
    display: flex;
    padding: 0 7px 0 5px;
    justify-content: space-between;
}

.skillHeader span {
    font-size: 12px;
    color: #364E66;
}

.skillName {
    letter-spacing: -0.5px;
    font-weight: 600;

}

.skillWrite {
    letter-spacing: -0.8px;
    font-weight: 600;

}

.skillBar {
    position: relative;
    width: 130px;
    height: 14px;
    background-color: #364e66;
    border-radius: 10px;
     overflow: hidden;
}

.skillBar .fill {
  position: absolute;
  left: 0; 
  top: 0; 
  bottom: 0;
  width: 0;                 /* 시작값 */
  background: #f9c300;      /* 채워질 색 */
  border-radius: inherit;
   transition: width 1.5s ease; /* 애니메이션 */
}

/* inview가 붙으면 var(--w)까지 차오르게 */
.inview .fill {
  width: var(--w);
}


