#main {
  width: 100%;
  height: 950px;
  box-sizing: border-box;
    scroll-margin-top: 80px;

}

#main .intro {
  width: 100%;
  max-width: 1440px;
  height: 950px;
  background-color: #ffffff;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.introText {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: flex-start;
  justify-content: center;
  gap: 40px;
}

.introText h1 {
  font-size: 65px;
  color: #ffcc00;
  line-height: 1.3;
}

.introText h1 span {
  text-shadow:
    1px 0 #8d8d8f,
    -1px 0 #8d8d8f,
    0 1px #8d8d8f,
    0 -1px #8d8d8f;
  font-weight: 600;
}

.introText h1 span strong {
  color: #ffb100;
  font-weight: 600;
}

.introText h1 span:nth-of-type(2) strong,
.introText h1 span:nth-of-type(3) strong {
  font-size: 58px;
}

.introText p {
  font-size: 35px;
  color: #364e66;
  font-weight: 400;
  line-height: 1.8;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  margin-left: 2px;
}

.introImage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 556px;
  height: 700px;
  border-radius: 50%;
  background-color: #cacaca;
  overflow: hidden; /* 원형 바깥 이미지 잘림 방지 */
}

.introImage img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* 이미지가 원형 안에 꽉 차도록 */
}