#project {
    display: flex;
    align-items: center;
    margin: 0 auto;
    width: 100%;
    height: 950px;
    box-sizing: border-box;

}

.projectContainer {
    width: 1440px;
    margin: 30px auto 0;
    display: flex;
    box-sizing: border-box;
    border: 3px solid #f9c300;
}

/* 부모 규격은 그대로 사용 (#project, .projectContainer) */

/* 화살표 */
.projArrow {
    align-self: center;
    width: 48px;
    height: 48px;
    background: rgba(255, 255, 255, .15);
    color: #1e3a5f;
    font-size: 34px;
    line-height: 48px;
    cursor: pointer;
    transition: transform .15s ease, background .15s ease;
}

.projArrow:hover {
    transform: scale(1.06);
    background: rgba(255, 255, 255, .25);
}

.projArrow.left {
    margin-right: 16px;
}

.projArrow.right {
    margin-left: 16px;
}

/* 카드 컨테이너(1440×950 내부에서 패딩만 줌) */
.projCard {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    height: 100%;
    padding: 28px 28px 20px;
    box-sizing: border-box;
    overflow: hidden;
    /* 부모 높이 넘지 않도록 */
}

/* 상단 제목/스택 */
.projHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    padding-bottom: 10px;
    border-bottom: 2px solid #f9c300;
    margin-bottom: 30px;
}

.projTitle {
    margin: 0;
    font-size: 28px;
    letter-spacing: 1px;
    color: #1e3a5f;
    font-weight: 800;
}

.projStack {
    display: flex;
    gap: 8px;
    margin: 0;
    padding: 0;
    list-style: none;
    flex-wrap: wrap;
}

.stackItem {
    padding: 6px 10px;
    font-size: 12px;
    border-radius: 50px;
    background: #eef2ff;
    color: #334155;
    border: 1px solid #c7d2fe;
}

/* 메인 2열 */
.projMain {
    display: grid;
    grid-template-columns: 640px 1fr;
    gap: 15px;
}

/* 모니터 블록 */
.monitorBlock {
    display: flex;
    flex-direction: column;
    border-radius: 14px;
    box-sizing: border-box;
    min-height: 0;
}

.monitorFrame {
    width: 592px;
    height: 419px;
    margin: 0 auto;
    overflow: hidden;
}

.monitorFrame img {
    width: 592px;
    height: 419px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* 키보드 장식 */
.keyboard {
    width: 400px;
    margin: 0 auto;
    border: 2px solid #364e66;
    border-radius: 10px;
    padding: 10px;
    background: #f8fbff;
}

.kbTop {
    display: flex;
    justify-content: center;
    gap: 8px;
    margin-bottom: 8px;
}

.kbGrid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 6px;
    margin-bottom: 8px;
}

.kbBottom {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 8px;
}

.key {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    background: #f9c300;
    color: #152235;
    border: 2px solid #364e66;
    font-weight: 700;
}

.key.sm {
    height: 18px;
}

.key.md {
    padding: 6px 8px;
    font-size: 12px;
}

.key.lg {
    padding: 8px 10px;
    font-size: 12px;
}

.key.xl {
    padding: 10px 0;
    font-size: 13px;
}

.key.ghost {
    background: #f9c300;
}

/* 오른쪽 정보 */
.projInfo {
    background: #fff;
    border-radius: 14px;
    padding: 18px;
    box-sizing: border-box;
    overflow: auto;
    /* 텍스트 길어도 부모 높이 유지 */
}

.projSub {
    margin: 15px 0;
    font-size: 25px;
    color: #364e66;
    font-weight: 800;
}

.proinfo01 p {
    font-size: 16px;
    line-height: 1.5;
}

.projMeta {
    margin: 15px 0;
}


.projMeta p {
    color: #364e66;
}

.projMeta p:first-child {
    margin: 15px 0;
    font-size: 25px;
    font-weight: 800;
}

.projMeta p:nth-child(2) {
    line-height: 1.5;
    font-size: 16px;
}

.projFeat {
    margin: 15px 0;
}

.projFeat h4 {
    margin: 15px 0;
    font-size: 25px;
    color: #22324a;
    font-weight: 800;
}

.projFeat ul {
    font-size: 16px;
}

.projFeat ul li {
    line-height: 1.5;

}

/* 하단 칩 */
.projChips {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0 0;
}

.chip {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 50px;
    font-size: 30px;
    border-radius: 100px;
    font-weight: 800;
    letter-spacing: -0.5px;
    text-align: center;
    background: #f9c300;
    color: #364e66;
    cursor: pointer;
}

/* 모니터 프레임 */
.monitorFrame {
  width: var(--mf-w, 592px);
  height: var(--mf-h, 419px);
  margin: 0 auto;
  overflow: hidden;
}

.monitorFrame img {
  width: 100%;
  height: 100%;
  object-fit: var(--mf-fit, cover);
}

/* 키보드: 프레임 폭에 어느 정도 맞춰 줄이기 (선택) */
.keyboard {
  width: min(400px, calc(var(--mf-w, 592px) * 0.9)); /* 프레임 폭의 90% 또는 400 중 더 작은 값 */
  margin: 10px auto 0;
  border: 2px solid #364e66;
  border-radius: 10px;
  padding: 10px;
  background: #f8fbff;
}
